let Complex = require('complex.js');
const Polynomial = require('polynomial');
const { JSXGraph } = require('jsxgraph');

var board = newBoard();

function newBoard() {
    return JSXGraph.initBoard('roots', {
        boundingBox: [-5, -5, 5, 5],
        axis: true
    });
}
function clearBoard() {
    JSXGraph.freeBoard(board);
    board = newBoard();
}
function resetBoard(plotType) {
    Polynomial.setField('C');

    var z = board.create('point', [0, 0], {'name': 'z', 'color': 'black'});

    function polyz(type, z) {
        let polys = {
            'symmetric': Polynomial({'4': 1, '1': z.neg(), '0': -2}),
            'alternating': Polynomial({'4': 81, '2': -216, '1': z.mul(Complex(192, 0)).sub(Complex(384, 0)), '0': z.mul(Complex(192, 0)).sub(Complex(192, 0))}),
            'dihedral': Polynomial({'4': 1, '2': -2, '0': Complex(3, 0).sub(z)}),
            'klein': Polynomial({'4': 1, '2': z.neg(), '0': -1}),
            'cyclic': Polynomial({'4': 1, '0': z.neg().sub(Complex(2, 0))})
        }
        return polys[type];
    }

    const b = 2.0867794;
    const e = Complex(b, b);
    const i = Complex(0, 1);
    badPoints = {
        'symmetric': [e.mul(-1), e.mul(i), e.mul(i.neg())],
        'klein': [Complex(0, 2), Complex(0, -2)],
        'dihedral': [Complex(2, 0), Complex(3, 0)],
        'alternating': [Complex(1, 0), Complex(2, 0)],
        'cyclic': [Complex(-2, 0)]
    }

    function newtons(f, w) {
        var root = w;
        for (let i = 0; i < 9; i++) {
            root = root.sub(f.eval(root).div(f.derive().eval(root)));
        }
        return root;
    }


    function getRoots(type) { 
        let approximateSpec =  {
            'alternating': [Complex(-1, 0), Complex(2, 0), Complex(-0.5, 1), Complex(-0.5, -1)],
            'dihedral': [Complex(1, 0.5), Complex(-1, 0.5), Complex(1, -0.5), Complex(-1, -0.5)],
        };
        var approximate = [Complex(1, 0), Complex(-1, 0), Complex(0, 1), Complex(0, -1)];
        if (type in approximateSpec) {
            approximate = approximateSpec[type];
        }
        let f = polyz(plotType, Complex(0,0));
        return approximate.map(guess => newtons(f, guess));
    }

    badPoints[plotType].forEach((bp, i) => board.create('point', [bp.re, bp.im], {'fixed': true, 'name': `BP ${i + 1}` })); 

    const initial_roots = getRoots(plotType);

    var rootPoints = [];
    for (let i = 0; i < initial_roots.length; i ++) {
        rootPoints.push(board.create('point', [function () {
            if (z.X() == 0 && z.Y() == 0) {
                return initial_roots[i].re;
            } else {
                root = Complex(rootPoints[i].X(), rootPoints[i].Y());
                zcmp = Complex(z.X(), z.Y());
                f = polyz(plotType, zcmp);
                return newtons(f, root).re;
            }
        }, function () {
            if (z.X() == 0 && z.Y() == 0) {
                return initial_roots[i].im;
            } else {
                root = Complex(rootPoints[i].X(), rootPoints[i].Y());
                zcmp = Complex(z.X(), z.Y());
                f = polyz(plotType, zcmp);
                return newtons(f, root).im;
            }
        }
    ], {'fixed': true, 'color': 'blue', 'name': `Root ${i + 1}`}));
    }

    let polyStrings = {
        'dihedral': 'w^4 - 2w^2 + 3 - z',
        'alternating': '81w^4 - 216w^2 - (192z - 384)w + (192z - 240)',
        'klein': 'w^4 - zw^2 - 1',
        'symmetric': 'w^4 - zw - 2',
        'cyclic': 'w^4 - z - 2'

    }


    let polyheader = document.getElementById('polynomial-header');
    polyheader.innerText = `Root Plot for P(w, z) = ${polyStrings[plotType]}`;
}

resetBoard('symmetric');

['symmetric', 'alternating', 'dihedral', 'klein', 'cyclic'].forEach(type => {
    document.getElementById(type).onclick = () => {
        clearBoard();
        resetBoard(type);
    };
})
